




































































































































@import '~@assets/style/essentials'

.skillsPage
  position: relative
  .skillHeading
    text-transform: uppercase
  .skillsSummary
    li
      span
        float: right
        padding-left: $base-spacing
  .languages
    .languageName
      display: inline-block
    .languageLevel
      display: inline-block
    .languageName~.languageLevel
      &::before
        padding: 0 $base-spacing
        content: '->'


  @media(min-width: $desktop)
    .languages
      .languageName
        display: block
      .languageLevel
        display: block
      .languageName~.languageLevel
        &::before
          display: none

    .others
      .otherTitle
        display: inline-block
      .otherTitle~.otherTitle
        &::before
          padding: 0 $base-spacing
          content: '|'
