


























































































@import '~@assets/style/essentials'

.contactForm
  .baseInput
    box-sizing: border-box
    width: 700px
    max-width: 100%
    height: 50px
    padding: $base-spacing
    margin-bottom: $base-spacing
    border-style: solid
    border-width: 5px
    border-image: url(~@assets/img/border.png) 10 round

    input
      width: 100%
      font-weight: bold
      color: #ddd
      background: none
      border: none
    .errorLine
      display: block
      line-height: 30px
  .textArea
    width: 700px
    max-width: 100%
    margin-bottom: $base-spacing
    textarea
      box-sizing: border-box
      width: 100% !important
      max-width: 100%
      height: 250px
      max-height: 50vh
      padding: $base-spacing
      font-weight: bold
      color: #ddd
      background: none
      border-style: solid
      border-width: 5px
      border-image: url(~@assets/img/border.png) 10 round
