








































@import '~@assets/style/essentials'

.projectCover
  z-index: 10
  max-width: 100%
  padding: 5px 10px 0
  margin-bottom: $base-spacing
  img
    width: 100%
    height: auto
    &.filtered
      filter: grayscale(1)
    &.colored
      position: absolute
      top: 5px
      left: 10px
      z-index: 0
      width: calc(100% - 20px)
      opacity: 0.2
      transition: opacity .3s ease-in-out
  .projectTitle
    position: absolute
    bottom: 0
    &.right
      right: $base-spacing * 2
    &.left
      left: $base-spacing * 2
  &.noBorders
    padding: 0
    .borders
      display: none

@media(min-width: $desktop)
  .projectCover
    &:hover
      img.colored
        opacity: 1
